@charset "utf-8";

/* -------------------------------------------*/

$c-text-base: #252525;
$c-text-main: #303030;

$c-white: #fff;
$c-black: #000;
$c-gray: #eaeaea;
$c-gray2: #cccccc;
$c-orange: #ed7b27;
$c-orange2: #fffbf1;
$c-yellow: #f6ff00;
$c-green: #319a33;
$c-red: #e8573d;
$c-blue: #2d5ece;

@mixin c-grad--main {
  background: rgb(255, 255, 255);
  background: linear-gradient(90deg, rgba(255, 255, 255, 1) 0%, rgba(200, 201, 201, 1) 100%);
}

@mixin c-grad--rev {
  background: rgb(0, 0, 0);
  background: linear-gradient(90deg, rgba(0, 0, 0, 1) 0%, rgba(61, 70, 75, 1) 100%);
}

