@charset "utf-8";
@use "font";
@use "color";
@use "mixin" as mix;

/* -------------------------------------------*/
@media all and (-ms-high-contrast: none) {
  html,
  body {
    display: none;
  }
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

html {
  -ms-overflow-style: scrollbar;
  overflow: auto;
  font-size: 62.5%;
  @include mix.mq(header, max) {
    font-size: 55.5%;
  }
  @include mix.mq(lg, max) {
    font-size: 62.5%;
  }
}

body {
  position: relative;
  height: 100%;
  overflow: hidden;
  font-size: font.$base-fz;
  font-weight: font.$base-wg;
  line-height: font.$base-lh;
  font-family: font.$base-ff;
  letter-spacing: font.$base-ls;
  color: color.$c-text-base;
  @include mix.mq(lg, max) {
    width: 100%;
    min-width: 100%;
    overflow-x: hidden;
  }
}

[tabindex="-1"]:focus {
  outline: 0 !important;
}

section {
  position: relative;
  width: 100%;
}

a,
button {
  color: inherit;
  text-decoration: none;
}

picture {
  display: block;
  line-height: 0;
}

img {
  width: 100%;
  height: auto;
  max-width: 100%;
  vertical-align: middle;
}

svg:not(:root) {
  overflow: hidden; // Hide the overflow in IE
}

input,
button,
select,
optgroup,
textarea {
  vertical-align: middle;
  position: relative;
  border: none;
  border: none;
  margin: 0;
  background-color: transparent;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -webkit-border-radius: 0;
  -webkit-appearance: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

button,
input {
  overflow: visible; // Show the overflow in Edge
}

button,
select {
  text-transform: none; // Remove the inheritance of text transform in Firefox
}

button,
html [type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

input[type="radio"],
input[type="checkbox"] {
  box-sizing: border-box;
  padding: 0;
}

input[type="date"],
input[type="time"],
input[type="datetime-local"],
input[type="month"] {
  -webkit-appearance: listbox;
}

textarea {
  overflow: auto;
  resize: vertical;
}

fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  border-offset: -2px; // 2. Correct the border style in Safari.
  -webkit-appearance: none;
}

[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  font: inherit;
  -webkit-appearance: button;
}

output {
  display: inline-block;
}

@include mix.mq {
  a[href^="tel:"] {
    pointer-events: none;
  }
}
